var thresholds = [
    {
        value: 10000000000,
        unit: 'B'
    },
    {
        value: 1000000,
        unit: 'M'
    },
    {
        value: 1000,
        unit: 'K'
    }
];
export var displayNumber = function (n) {
    var display = String(n);
    thresholds.some(function (threshold) {
        if (n >= threshold.value) {
            var roundedN = n / threshold.value;
            if (roundedN >= 10) {
                roundedN = Math.round(roundedN);
            }
            else if (roundedN >= 10) {
                roundedN = Math.round(roundedN * 10) / 10;
            }
            else {
                roundedN = Math.round(roundedN * 100) / 100;
            }
            display = "".concat(roundedN).concat(threshold.unit);
            return true;
        }
        return false;
    });
    return display;
};
